type arrCardType<T> = {
  main: Array<{
    title: T;
    list: Array<{ text: T; path: T }>;
    footer: T;
  }>;
};

const initialState: arrCardType<string> = {
  main: [
    {
      title: " ",
      list: [
        {
          text: "   ?",
          path: "numperNat",
        },
        {
          text: "   ",
          path: "comparisonNumb",
        },
        {
          text: "  ",
          path: "plusNumber",
        },
        {
          text: "  ",
          path: "minusNumber",
        },
        {
          text: "  ",
          path: "multiplicationNumber",
        },
        {
          text: "  ",
          path: "divisionNumber",
        },
        {
          text: " ",
          path: "factorization",
        },
      ],
      footer: ` 2023 - ${new Date().getFullYear()},  `,
    },
  ],
};

const STATIC = "static";

interface TypeAction<T> {
  type: T;
}

type Action = TypeAction<typeof STATIC>;

const mainPageReducer = (state = initialState, action: Action) => {
  switch (action.type) {
    case STATIC: {
      return { ...state };
    }
    default:
      return state;
  }
};

export const staticAction = () => ({ type: STATIC });
export default mainPageReducer;
